<template>
<div class="subpage--main">
  <Card>
    <p slot="title">分析挖掘流程列表</p>
    <div class="condition">
      <label class="condition__item">
        文件名：
        <Input v-model="table.unsyncQuery.fName" placeholder="请输入流程名称" style="width: 180px" />
      </label>
      <label class="condition__item">
        文件类型：
        <Select v-model="table.unsyncQuery.fType" clearable style="width:120px;">
          <Option
            v-for="extension in EXTENSION_LIST"
            :key="extension"
            :value="extension"
          >{{extension}}</Option>
        </Select>
      </label>
      <label class="condition__item">
        备注
        <Input v-model="table.unsyncQuery.fRemark" placeholder="请输入流程备注" style="width: 180px" />
      </label>
      <Button
        class="condition__item"
        type="primary"
        icon="ios-search"
        :loading="table.loading"
        @click="table.apply"
      >搜索</Button>
      <Button
        class="condition__item"
        type="info"
        icon="plus-round"
        @click="showUpload"
      >添加</Button>
    </div>
    <EasyTable
      class="table"
      :table="table"
      border
      stripe
    />
  </Card>
  <Modal
    v-model="uploadModal.open"
    title="上传文件"
    width="360"
  >
    <file-upload
      ref="upload"
      v-model="uploadModal.files"
      class="ktl-upload"
      :post-action="$api.kettle.uploadFile.config.url"
      :headers="$store.getters.authHeaders"
      @input-filter="changeFileExtenstions"
      @input-file="fileInput"
    >
      <template v-if="uploadModal.files.length > 0">
        {{uploadModal.files[0].name}}
      </template>
      <template v-else>
        <Icon type="android-add-circle" size="18" style="position:relative;top:2px;"/> 选择文件
      </template>
    </file-upload>
    <Input
      v-model="uploadModal.remark"
      type="textarea"
      placeholder="备注"
      :autosize="{minRows: 2, maxRows: 5}"
    />
    <Button
      slot="footer"
      v-if="!$refs.upload || !$refs.upload.active"
      type="primary"
      size="large"
      long
      @click.prevent="$refs.upload.active = true"
    >上传</Button>
  </Modal>
</div>
</template>

<script>
import {
  Button
} from 'iview'
import fileUpload from 'vue-upload-component'
import EasyTable, { createTable } from '@/components/EasyTable'
import {
  dateFormat,
} from '@/utils/date'

export default {
  components: {
    EasyTable,
    fileUpload,
  },
  data() {
    return {
      uploadModal: {
        open: false,
        files: [],
        remark: ''
      },
      EXTENSION_LIST: ['kjb', 'ktr', 'py', 'jar'],
      table: createTable({
        query: {
          fRemark: null,
          fName: null,
          fType: null
        },
        loadData: async ({
          PAGE_SIZE,
          current,
          query,
        }) => {
          const {
            list: items,
            total
          } = await this.$api.kettle.listFiles.send({
            pageSize: PAGE_SIZE,
            pageNum: current,
            ...query
          })
          return {
            items,
            total
          }
        },
        COLUMNS: [
          {
            title: '序号',
            type: 'index',
            width: 70,
            align: 'center'
          },
          {
            title: '作业ID',
            key: 'fId',
            align: 'center'
          },
          {
            title: '文件名',
            key: 'fName',
            align: 'center'
          },
          {
            title: '描述',
            key: 'fRemark',
            align: 'center'
          },
          {
            title: '创建时间',
            key: 'createDate',
            align: 'center',
            render: (h, params) => <span>{dateFormat(params.row.createDate)}</span>
          },
          {
            title: '类型',
            key: 'fType',
            align: 'center'
          },
          {
            title: '大小',
            key: 'fSize',
            width: 80,
            align: 'center'
          },
          {
            width: 180,
            title: '操作',
            align: 'center',
            render: (h, { row }) => (
              <div>
                {
                  row.fType === 'kjb' ? (
                    <Button
                      type="success"
                      size="small"
                      style="marginRight: 5px"
                      onClick={() => this.showDetail(row)}
                    >打开</Button>
                  ) : null
                }
                {
                  row.fType === 'kjb' ? (
                    <Button
                      type="primary"
                      size="small"
                      style="marginRight: 5px"
                      onClick = {() => this.startFile(row)}
                    >启动</Button>
                  ) : null
                }
                <Button
                  type="error"
                  size="small"
                  onClick={() => this.deleteFile(row)}
                >删除</Button>
              </div>
            )
          }
        ],
      })
    }
  },
  created() {
    this.table.apply()
  },
  methods: {
    deleteFile({ fId }) {
      this.$Modal.confirm({
        title: '系统提示',
        content: '您确定要删除此文件？',
        onOk: async () => {
          try {
            await this.$api.kettle.deleteFile.send({ fId })
            this.$Message.success('成功删除文件')
          } finally {
            this.table.refresh()
          }
        }
      })
    },
    async startFile({ fId }) {
      try {
        await this.$api.kettle.executeJob.send({ fId })
        this.$Message.success('成功启动任务！')
      } finally {
        // this.table.refresh()
      }
    },
    async showDetail({ fId }) {
      this.$router.push({
        name: 'KettleDetail',
        query: {
          fId
        }
      })
    },
    showUpload() {
      this.uploadModal.open = true
    },
    changeFileExtenstions(newFile, oldFile, prevent) {
      if (newFile && !oldFile) {
        // Add file
        // Will not be added to files
        if (!/\.(kjb|ktr|jar|py)$/i.test(newFile.name)) {
          this.$Message.error(`只能上传${this.EXTENSION_LIST.join('|')}格式的文件`);
          return prevent();
        }
      }

      if (newFile && oldFile) {
        if (newFile.active !== oldFile.active) {
          let nameArr = newFile.name.split('.')
          let extension = nameArr.pop()
          newFile.data = {
            fName: nameArr.join('.'),
            fType: extension,
            fRemark: this.uploadModal.remark
          };
        }

        // Update file
        // Increase the version number
        if (!newFile.version) {
          newFile.version = 0;
        }
        newFile.version++;
      }
    },
    resetUpload() {
      Object.assign(this.uploadModal, {
        files: [],
        remark: null
      })
    },
    fileInput(newFile, oldFile) {
      if (newFile && oldFile) {
        if (newFile.success !== oldFile.success) {
          this.uploadModal.open = false
          if (newFile.response.success) {
            this.$Message.success('成功上传文件')
            this.resetUpload()
            this.table.refresh()
          } else {
            this.$Modal.error({
              title: '系统提示',
              content: newFile.response.error.errorMessage
            });
          }
        }
      }
    },
  },
};
</script>

<style lang="scss" scoped>
.klt-show {
  width: 100%;
  height: 100%;
  position: absolute;
  left: 0;
  top: 0;
  z-index: 1;
  background: rgba(255, 255, 255, 0.9);
}

.ktl-upload {
  height: 32px;
  background: $themeColor;
  display: inline-block;
  line-height: 32px;
  color: #fff;
  border-radius: 6px;
  font-size: 14px;
  width: 100%;
  cursor: pointer;
}
</style>
